.tour {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    position: relative;
    padding: 32px;
    margin-right: -16px;
    margin-left: -16px;
    min-height: auto;
    width: calc(100% + 32px);
    background-color: var(--green);
    
    &::before {
        content: '';
        position: absolute;
        top: 38px;
        left: 16px;
        display: block;
        width: calc(100% - 32px);
        height: calc(100% - 54px);
        border: 1px solid var(--white);
    }
}

.tour__head {
    position: relative;
    padding-left: 24px;
    padding-right: 24px;
    font-family: Elisabethische, serif;
    font-size: 1.5rem;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--white);
    background-color: var(--green);
}

.tour__content {
    position: relative;
    padding-top: 100%;
    width: 100%;
}

.tour__media {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    transform: translate(-50%, -50%);
    overflow: hidden;

    iframe {
        border:0;
    }

    img {
        height: 100%;
        object-fit: cover;
    }
}

@media (min-width: 500px) {
    .tour__content {
        padding-top: 70%;
    }
}

@media (min-width: $small) {
    .tour__content {
        padding-top: 50%;
    }


}

@media (min-width: 1000px) {
    .tour {
        padding: 48px;
        padding-top: 31px;
        margin-right: 0;
        margin-left: 0;
        width: 100%;
        
        &::before {
            top: 53px;
            left: 24px;
            width: calc(100% - 48px);
            height: calc(100% - 77px);
        }
    }

    .tour__head {
        font-size: 2.25rem;
    }

    .tour__content {
        padding-top: 37%;
    }
}